.imgCover{
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}
    
.imgBgBottom {
  background-position: bottom;
  background-size: cover;
  background-repeat: no-repeat;
}


//center
.center{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
}

//logo
.logo {
  display:block; /*使a成為區塊元素可以長寬高*/
  background: url('../images/logo/logo.png') no-repeat; /*設定要使用的圖片，且不要重複*/
  width: 190px; /*需求的寬度*/
  height: 145px; /*需求的高度*/
  text-indent: 101%; /*讓文字首行縮排到範圍外*/
  overflow: hidden; /*超過範圍的就自動隱藏*/
  white-space: nowrap; /*不要斷行就一排，所以只有"首行"*/
  background-size: cover;
  // @include media-breakpoint-up(md) {
  //   width: 205px;
  //   height: 73px;
  // }
  // @include media-breakpoint-up(lg) {
  //   width: 253px; 
  //   height: 90px;
  // }
  
}

//googleMap
.googleMap {
  overflow: hidden;
   iframe{
       width: 100vw;
       height: 100vh;
      display:block;
  }
}

// loader
.loadPage{
  min-width: 100%;
  min-height: 100%;
  background-color: $white;
  opacity:0.7;
  transition:  3s ease-in-out; 
}
.loadPage,.loader {
	position: fixed;
	top: 50%;
	left: 50%;
  transform: translateX(-50%) translateY(-50%);
	z-index: 10 !important;
}
.ball-beat > div {
  background-color: $primary;
}

// 行數過長的文字隱藏
.ellipsis5 {
  overflow: hidden;
  display: -webkit-box;
  text-overflow: ellipsis;
  -webkit-line-clamp: 5; /*行數*/
  -webkit-box-orient: vertical;
  white-space: normal;
}

